.toggle{
    display: flex;
    justify-content: space-between;
    border:3px solid orangered;
    cursor: pointer;
    border-radius: 1rem;
    position: relative;
    padding:2px
}
.toggle>*{
    width:1rem;
    height:1rem;
    color:orangered
}
.t-button{
    position: absolute;
    border-radius: 100%;
    background-color: orangered;
}
@media screen and (max-width : 480px){
  .toggle{
    margin-left:25%
  }
}